import React, { useState } from 'react';
import { NavLink, useLocation, useNavigate } from 'react-router-dom';
import { LayoutDashboard, History, Coffee, Sun, Moon, Menu, X, Bean, LogOut, User, Edit2 } from 'lucide-react';
import { useApp } from '../context/AppContext';
import { useAuth } from '../context/AuthContext';

interface LayoutProps {
  children: React.ReactNode;
}

const Layout: React.FC<LayoutProps> = ({ children }) => {
  const location = useLocation();
  const navigate = useNavigate();
  const { state, toggleTheme } = useApp();
  const { user, signOut } = useAuth();
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  const navItems = [
    { path: '/', label: 'Dashboard', icon: <LayoutDashboard size={20} /> },
    { path: '/historico', label: 'Histrico', icon: <History size={20} /> },
    { path: '/cafes', label: 'Cafs', icon: <Bean size={20} /> },
  ];

  const toggleSidebar = () => setIsSidebarOpen(!isSidebarOpen);

  const handleLogout = async () => {
    try {
      await signOut();
    } catch (error) {
      console.error('Erro ao sair:', error);
    }
  };

  return (
    <div className="h-screen bg-natural-50 dark:bg-gray-900 font-sans text-black dark:text-white transition-colors duration-200 flex overflow-hidden">
      
      {/* Mobile Header */}
      <div className="lg:hidden fixed top-0 left-0 right-0 h-16 bg-white dark:bg-gray-800 border-b border-black dark:border-white flex items-center justify-between px-4 z-30 transition-colors">
        <div className="flex items-center gap-3">
          <div className="bg-black dark:bg-white text-white dark:text-black p-2 rounded-lg">
            <Coffee size={24} />
          </div>
          <h1 className="text-lg font-bold tracking-tight text-black dark:text-white truncate">Studio Gro</h1>
        </div>
        <button onClick={toggleSidebar} className="p-2 text-black dark:text-white">
          {isSidebarOpen ? <X size={24} /> : <Menu size={24} />}
        </button>
      </div>

      {/* Sidebar Overlay (Mobile) */}
      {isSidebarOpen && (
        <div 
          className="lg:hidden fixed inset-0 bg-black/50 z-40 backdrop-blur-sm"
          onClick={() => setIsSidebarOpen(false)}
        />
      )}

      {/* Sidebar Navigation */}
      <aside className={`
        fixed lg:static inset-y-0 left-0 z-50
        w-64 bg-white dark:bg-gray-800 border-r border-black dark:border-white
        transform transition-transform duration-300 ease-in-out
        ${isSidebarOpen ? 'translate-x-0' : '-translate-x-full lg:translate-x-0'}
        flex flex-col h-full
      `}>
        <div className="h-16 flex items-center px-6 border-b border-natural-100 dark:border-gray-700 shrink-0">
           <div 
             className="flex items-center gap-3 cursor-pointer"
             onClick={() => navigate('/')}
           >
              <div className="bg-espresso-600 text-white p-2 rounded-lg">
                <Coffee size={24} />
              </div>
              <div className="leading-tight">
                <h1 className="text-base font-bold text-black dark:text-white">Studio Gro</h1>
                <p className="text-xs text-gray-500 dark:text-gray-400 font-medium">Coffee Roaster</p>
              </div>
            </div>
        </div>

        {/* User Info */}
        <div className="px-6 py-4 border-b border-natural-100 dark:border-gray-700 shrink-0">
          <p className="text-xs text-gray-500 dark:text-gray-400 font-bold uppercase mb-1">Usurio</p>
          <div className="flex items-center gap-2">
            <p className="text-sm font-bold text-black dark:text-white truncate flex-1" title={user?.user_metadata?.name || user?.email}>
              {user?.user_metadata?.name || user?.email}
            </p>
            <button 
              type="button"
              onClick={(e) => {
                e.stopPropagation();
                navigate('/perfil');
                setIsSidebarOpen(false);
              }}
              className="p-1.5 rounded-md hover:bg-gray-100 dark:hover:bg-gray-700 text-gray-500 dark:text-gray-400 transition-colors flex-shrink-0 cursor-pointer z-10"
              title="Editar Perfil"
            >
              <Edit2 size={16} />
            </button>
          </div>
        </div>

        <nav className="flex-1 px-4 py-6 space-y-2 overflow-y-auto">
          {navItems.map((item) => (
            <NavLink
              key={item.path}
              to={item.path}
              onClick={() => setIsSidebarOpen(false)}
              className={({ isActive }) =>
                `flex items-center gap-3 px-4 py-3 rounded-lg text-sm font-bold transition-all ${
                  isActive
                    ? 'bg-espresso-600 text-white shadow-md'
                    : 'text-gray-600 dark:text-gray-300 hover:bg-espresso-50 dark:hover:bg-gray-700/50 hover:text-espresso-700 dark:hover:text-white'
                }`
              }
            >
              {item.icon}
              {item.label}
            </NavLink>
          ))}
        </nav>

        <div className="p-4 border-t border-natural-100 dark:border-gray-700 space-y-2 shrink-0">
          <button
            onClick={toggleTheme}
            className="w-full flex items-center justify-center gap-2 p-2 rounded-lg border border-natural-100 dark:border-gray-600 hover:bg-natural-50 dark:hover:bg-gray-700 transition-colors text-black dark:text-white font-bold text-sm"
          >
            {state.theme === 'dark' ? <Sun size={18} /> : <Moon size={18} />}
            <span>{state.theme === 'dark' ? 'Modo Claro' : 'Modo Escuro'}</span>
          </button>
          
          <button
            onClick={handleLogout}
            className="w-full flex items-center justify-center gap-2 p-2 rounded-lg text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors font-bold text-sm"
          >
            <LogOut size={18} />
            <span>Sair</span>
          </button>
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 min-w-0 pt-16 lg:pt-0 pb-16 lg:pb-0 overflow-y-auto h-full">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {children}
        </div>
      </main>

      {/* Bottom Navigation (Mobile) */}
      <nav className="lg:hidden fixed bottom-0 left-0 right-0 bg-white dark:bg-gray-800 border-t border-black dark:border-white flex justify-around items-center h-16 z-30 px-2 pb-safe">
        {navItems.map((item) => (
          <NavLink
            key={item.path}
            to={item.path}
            className={({ isActive }) =>
              `flex flex-col items-center justify-center p-2 rounded-lg w-full transition-all ${
                isActive
                  ? 'text-espresso-600 dark:text-espresso-400'
                  : 'text-gray-400 dark:text-gray-500 hover:text-gray-600 dark:hover:text-gray-300'
              }`
            }
          >
            {({ isActive }) => (
              <>
                <div className={`p-1 rounded-full ${isActive ? 'bg-espresso-50 dark:bg-espresso-900/30' : ''}`}>
                  {item.icon}
                </div>
                <span className={`text-[10px] font-bold mt-1 ${isActive ? 'text-espresso-600 dark:text-espresso-400' : ''}`}>
                  {item.label}
                </span>
              </>
            )}
          </NavLink>
        ))}
      </nav>
    </div>
  );
};

export default Layout;
