import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { useApp } from '../context/AppContext';
import { useToast } from '../context/ToastContext';
import { Plus, Edit2, Trash2, Coffee as CoffeeIcon } from 'lucide-react';
import Modal from './Modal';

interface CoffeeFormData {
  name: string;
  observations: string;
}

const CoffeeList: React.FC = () => {
  const { state, actions } = useApp();
  const { showToast } = useToast();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingId, setEditingId] = useState<string | null>(null);
  const [deleteId, setDeleteId] = useState<string | null>(null);

  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm<CoffeeFormData>();

  const openNew = () => {
    setEditingId(null);
    reset({ name: '', observations: '' });
    setIsModalOpen(true);
  };

  const openEdit = (coffee: { id: string; name: string; observations?: string }) => {
    setEditingId(coffee.id);
    setValue('name', coffee.name);
    setValue('observations', coffee.observations || '');
    setIsModalOpen(true);
  };

  const handleDeleteClick = (id: string) => {
    setDeleteId(id);
  };

  const confirmDelete = () => {
    if (deleteId) {
      actions.deleteCoffee(deleteId);
      showToast('Caf excludo com sucesso!', 'success');
      setDeleteId(null);
    }
  };

  const onSubmit = (data: CoffeeFormData) => {
    try {
      if (editingId) {
        actions.updateCoffee(editingId, data);
        showToast('Caf atualizado com sucesso!', 'success');
      } else {
        actions.addCoffee(data);
        showToast('Caf cadastrado com sucesso!', 'success');
      }
      setIsModalOpen(false);
      reset();
    } catch (error) {
      showToast('Erro ao salvar caf.', 'error');
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
        <div>
          <h2 className="text-2xl font-bold text-black dark:text-white">Gerenciar Cafs</h2>
          <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">Cadastre, edite ou remova os cafs disponveis.</p>
        </div>
        <button
          onClick={openNew}
          className="hidden sm:flex bg-espresso-600 text-white px-4 py-2 rounded-lg hover:bg-espresso-700 transition-colors shadow-sm items-center justify-center gap-2 font-bold"
        >
          <Plus size={20} />
          <span>Novo Caf</span>
        </button>
      </div>

      {/* FAB Mobile */}
      <button
        onClick={openNew}
        className="fixed bottom-20 right-4 sm:hidden z-40 bg-espresso-600 text-white p-4 rounded-full shadow-lg hover:bg-espresso-700 active:scale-95 transition-all"
        aria-label="Novo Caf"
      >
        <Plus size={24} />
      </button>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {state.coffees.map((coffee) => (
          <div key={coffee.id} className="bg-white dark:bg-gray-800 p-6 rounded-xl shadow-sm border border-natural-100 dark:border-gray-700 transition-colors relative group">
            <div className="flex items-start justify-between">
              <div className="flex items-center gap-3">
                <div className="bg-natural-50 dark:bg-gray-700 p-3 rounded-full text-black dark:text-white">
                  <CoffeeIcon size={24} />
                </div>
                <div>
                  <h3 className="text-lg font-bold text-black dark:text-white">{coffee.name}</h3>
                  {coffee.observations && (
                    <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">{coffee.observations}</p>
                  )}
                  <p className="text-xs text-gray-400 dark:text-gray-500 mt-2 font-mono">ID: {coffee.id}</p>
                </div>
              </div>
            </div>
            
            <div className="absolute top-4 right-4 flex gap-2 opacity-100 sm:opacity-0 sm:group-hover:opacity-100 transition-opacity">
              <button
              onClick={() => openEdit(coffee)}
              className="p-2 text-white bg-espresso-600 hover:bg-espresso-700 rounded-full transition-colors shadow-sm"
              title="Editar"
            >
              <Edit2 size={18} />
            </button>
            <button
              onClick={() => handleDeleteClick(coffee.id)}
              className="p-2 text-white bg-red-600 hover:bg-red-700 rounded-full transition-colors shadow-sm"
              title="Excluir"
            >
              <Trash2 size={18} />
            </button>
            </div>
          </div>
        ))}
      </div>

      <Modal
        isOpen={!!deleteId}
        onClose={() => setDeleteId(null)}
        title="Confirmar Excluso"
      >
        <div className="space-y-4">
          <div className="flex items-center justify-center p-4 bg-red-50 dark:bg-red-900/20 rounded-full w-16 h-16 mx-auto">
            <Trash2 size={32} className="text-red-600 dark:text-red-400" />
          </div>
          <div className="text-center space-y-2">
            <p className="text-black dark:text-white font-medium">
              Tem certeza que deseja excluir este caf?
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Esta ao no pode ser desfeita. Todo o histrico e estoque vinculados a este caf sero perdidos permanentemente.
            </p>
          </div>
          <div className="flex gap-3 mt-6">
            <button
              onClick={() => setDeleteId(null)}
              className="flex-1 py-2 px-4 text-sm font-bold text-gray-700 dark:text-gray-200 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg shadow-sm hover:bg-gray-50 dark:hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-espresso-500"
            >
              Cancelar
            </button>
            <button
              onClick={confirmDelete}
              className="flex-1 py-2 px-4 text-sm font-bold text-white bg-red-600 border border-transparent rounded-lg shadow-sm hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
            >
              Excluir
            </button>
          </div>
        </div>
      </Modal>

      <Modal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        title={editingId ? 'Editar Caf' : 'Novo Caf'}
      >
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          <div>
            <label className="block text-sm font-bold text-black dark:text-white mb-1">Nome do Caf</label>
            <input
              type="text"
              {...register('name', { required: 'Nome  obrigatrio' })}
              className="w-full rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium"
              placeholder="Ex: Colombia Huila"
            />
            {errors.name && <p className="text-xs text-red-500 mt-1">{errors.name.message}</p>}
          </div>

          <div>
            <label className="block text-sm font-bold text-black dark:text-white mb-1">Observaes (Opcional)</label>
            <textarea
              {...register('observations')}
              rows={3}
              className="w-full rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium"
              placeholder="Ex: Notas sensoriais, fornecedor..."
            />
          </div>

          <div className="flex gap-3 mt-4">
            <button
              type="button"
              onClick={() => setIsModalOpen(false)}
              className="flex-1 py-2 px-4 text-sm font-bold text-gray-700 dark:text-gray-200 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg shadow-sm hover:bg-gray-50 dark:hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-espresso-500"
            >
              Cancelar
            </button>
            <button
              type="submit"
              className="flex-1 bg-espresso-600 text-white py-2 px-4 rounded-lg hover:bg-espresso-700 transition-colors font-bold"
            >
              {editingId ? 'Salvar' : 'Cadastrar'}
            </button>
          </div>
        </form>
      </Modal>
    </div>
  );
};

export default CoffeeList;
