import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useApp } from '../context/AppContext';
import { useToast } from '../context/ToastContext';
import { useAuth } from '../context/AuthContext';
import { Plus, Minus } from 'lucide-react';
import { Movement } from '../types';

interface FormData {
  type: 'entrada' | 'saida';
  coffeeId: string;
  pr: string;
  date: string;
  quantity: number;
  observations: string;
  responsible: string;
}

interface MovementFormProps {
  onSuccess?: () => void;
  onCancel?: () => void;
  initialData?: Movement;
}

const MovementForm: React.FC<MovementFormProps> = ({ onSuccess, onCancel, initialData }) => {
  const { state, actions } = useApp();
  const { user } = useAuth();
  const { showToast } = useToast();
  
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    watch,
    setValue,
  } = useForm<FormData>();

  const selectedCoffeeId = watch('coffeeId');
  const movementType = watch('type');
  const selectedPr = watch('pr');

  useEffect(() => {
    if (initialData) {
      setValue('type', initialData.type);
      setValue('coffeeId', initialData.coffeeId);
      setValue('pr', initialData.pr);
      // Ensure date is in YYYY-MM-DD format for input
      const dateValue = initialData.timestamp ? new Date(initialData.timestamp).toISOString().split('T')[0] : '';
      setValue('date', dateValue);
      setValue('quantity', initialData.quantity);
      setValue('observations', initialData.observations || '');
      setValue('responsible', initialData.responsible || '');
    }
  }, [initialData, setValue]);

  const selectedCoffee = state.coffees.find(coffee => coffee.id === selectedCoffeeId);
  const selectedPrRoast = selectedCoffee?.roasts.find(r => r.pr === selectedPr);

  const onSubmit = (data: FormData) => {
    try {
      const quantityNum = Number(data.quantity);
      
      if (isNaN(quantityNum)) {
        showToast('A quantidade deve ser um nmero vlido.', 'error');
        return;
      }

      if (data.type === 'saida' && selectedCoffee) {
        let availableStock = selectedCoffee.roasts.reduce((sum, roast) => sum + roast.quantity, 0);
        
        if (initialData && initialData.type === 'saida' && initialData.coffeeId === data.coffeeId) {
           availableStock += initialData.quantity; // Add back the original amount for validation
        }

        if (quantityNum > availableStock) {
          showToast(`Quantidade insuficiente! Estoque disponvel: ${availableStock}g`, 'error');
          return;
        }
      }

      if (!user) {
        showToast('Erro: Usurio no identificado. Por favor, faa login novamente.', 'error');
        return;
      }

      const movementData = {
        type: data.type,
        coffeeId: data.coffeeId,
        coffeeName: selectedCoffee?.name || '',
        pr: data.pr,
        quantity: quantityNum,
        observations: data.observations,
        responsible: user.user_metadata?.name || user.email || 'Usurio',
      };

      if (initialData) {
        actions.updateMovement(initialData.id, movementData);
        showToast('Movimentao atualizada com sucesso!', 'success');
      } else {
        actions.addMovement(movementData);
        showToast('Movimentao registrada com sucesso!', 'success');
      }

      reset();
      if (onSuccess) onSuccess();
      
    } catch (error) {
      showToast('Erro ao processar movimentao.', 'error');
      console.error(error);
    }
  };

  return (
    <div className="space-y-4">
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
        {/* Tipo */}
        <div className="flex gap-4">
          <label className="flex items-center p-3 border border-natural-100 dark:border-gray-600 rounded-lg cursor-pointer hover:bg-natural-50 dark:hover:bg-gray-700 flex-1 justify-center transition-colors">
            <input
              type="radio"
              value="entrada"
              {...register('type', { required: true })}
              className="text-black focus:ring-espresso-500"
            />
            <span className="ml-2 font-bold text-black dark:text-white flex items-center gap-1">
              Entrada
            </span>
          </label>
          <label className="flex items-center p-3 border border-natural-100 dark:border-gray-600 rounded-lg cursor-pointer hover:bg-natural-50 dark:hover:bg-gray-700 flex-1 justify-center transition-colors">
            <input
              type="radio"
              value="saida"
              {...register('type', { required: true })}
              className="text-black focus:ring-espresso-500"
            />
            <span className="ml-2 font-bold text-black dark:text-white flex items-center gap-1">
              Sada
            </span>
          </label>
        </div>
        {errors.type && <p className="text-xs text-red-500">Selecione o tipo.</p>}

        {movementType && (
          <>
            {/* Caf */}
            <div>
              <label className="block text-sm font-bold text-black dark:text-white mb-1">Caf</label>
              <select
                {...register('coffeeId', { required: 'Selecione um caf' })}
                className="w-full rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium"
              >
                <option value="">Selecione...</option>
                {state.coffees.map((coffee) => (
                  <option key={coffee.id} value={coffee.id}>{coffee.name}</option>
                ))}
              </select>
              {errors.coffeeId && <p className="text-xs text-red-600 mt-1">{errors.coffeeId.message}</p>}
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-bold text-black dark:text-white mb-1">PR / Lote</label>
                {movementType === 'saida' ? (
                  <>
                    <select
                      {...register('pr', { required: 'Selecione o PR' })}
                      className="w-full h-10 rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium"
                      disabled={!selectedCoffeeId}
                    >
                      <option value="">Selecione...</option>
                      {selectedCoffee?.roasts.map((roast) => (
                        <option key={roast.id} value={roast.pr}>
                          {roast.pr}
                        </option>
                      ))}
                    </select>
                    {selectedPrRoast && (
                      <p className="text-xs text-gray-500 dark:text-gray-400 mt-1 font-medium">
                        Disponvel: {selectedPrRoast.quantity}g
                      </p>
                    )}
                  </>
                ) : (
                  <input
                    type="text"
                    {...register('pr', { required: 'Digite o PR' })}
                    className="w-full h-10 rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium placeholder-gray-400 dark:placeholder-gray-500"
                    placeholder="Digite o cdigo do PR"
                  />
                )}
                {errors.pr && <p className="text-xs text-red-600 mt-1">{errors.pr.message}</p>}
              </div>
              <div>
                <label className="block text-sm font-bold text-black dark:text-white mb-1">Qtd (g)</label>
                <input
                  type="number"
                  {...register('quantity', { required: true, min: 1, valueAsNumber: true })}
                  className="w-full h-10 rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium placeholder-gray-400 dark:placeholder-gray-500"
                  placeholder="0"
                />
                {errors.quantity && <p className="text-xs text-red-500">Qtd invlida.</p>}
              </div>
            </div>

            {/* Observations */}
            <div>
              <label className="block text-sm font-bold text-black dark:text-white mb-1">Observaes</label>
              <textarea
                {...register('observations')}
                className="w-full rounded-md border-natural-100 dark:border-gray-600 bg-white dark:bg-gray-700 shadow-sm focus:border-espresso-500 focus:ring-espresso-500 py-2 px-3 border text-black dark:text-white font-medium placeholder-gray-400 dark:placeholder-gray-500"
                rows={3}
                placeholder="Detalhes opcionais..."
              />
            </div>
          </>
        )}

        {/* Botes */}
        <div className="flex gap-3 pt-2 w-full">
          {onCancel && (
            <button
              type="button"
              onClick={onCancel}
              className="flex-1 py-2 text-sm font-bold text-gray-700 dark:text-gray-200 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg shadow-sm hover:bg-gray-50 dark:hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-espresso-500"
            >
              Cancelar
            </button>
          )}
          <button
            type="submit"
            className="flex-1 py-2 text-sm font-bold text-white bg-espresso-600 border border-transparent rounded-lg shadow-sm hover:bg-espresso-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-espresso-500"
          >
            {initialData ? 'Atualizar' : 'Registrar'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default MovementForm;